import { EventEmitter } from 'events';
import { IMessenger } from '@tencent/wxa-runtime-base';
import pulldownRefresh from './pulldownRefresh';
import uniqId from 'licia/uniqId';
import { log } from '@tencent/wxa-runtime-base';

const logger = log('wvBridge');

export default function (messenger: IMessenger) {
  const onEmitter = new EventEmitter();
  const subscribeEmitter = new EventEmitter();
  const callbacks: any = {};

  messenger.on('message', (msg: any) => {
    const { command, data } = msg;

    switch (command) {
      case 'WEBVIEW_INVOKE_CALLBACK': {
        const { callbackId, res } = data;
        logger.debug(res.errMsg, callbackId, res);
        if (callbacks[callbackId]) {
          callbacks[callbackId](res);
          delete callbacks[callbackId];
        }
        break;
      }
      case 'WEBVIEW_ON_EVENT': {
        logger.debug(data.eventName, data.data);

        onEmitter.emit(data.eventName, data.data);
        break;
      }
      case 'START_PULLDOWN_REFRESH': {
        logger.debug('start pulldown refresh');

        pulldownRefresh.trigger();
        break;
      }
      case 'STOP_PULLDOWN_REFRESH': {
        logger.debug('stop pulldown refresh');

        pulldownRefresh.reset();
        break;
      }
      case 'DISABLE_SCROLL_BOUNCE': {
        const { disable } = data;
        logger.debug('disable scroll bounce', disable);

        disable ? pulldownRefresh.disable() : pulldownRefresh.enable();
        break;
      }
      case 'APPSERVICE_PUBLISH': {
        subscribeEmitter.emit(data.eventName, data.data);
        break;
      }
    }
  });

  return {
    // 
    invoke(api: string, args: any, callback: any) {
      const callbackId = uniqId();
      logger.debug('invoke', api, callbackId, args);
      callbacks[callbackId] = callback;

      messenger.send({
        command: 'WEBVIEW_INVOKE',
        data: {
          api,
          args,
          callbackId,
        },
      });
    },
    //  appservice
    publish(eventName: string, data: any) {
      logger.debug('publish', eventName, data);

      data = JSON.parse(JSON.stringify(data));

      messenger.send({
        command: 'WEBVIEW_PUBLISH',
        data: {
          eventName,
          data,
        },
      });
    },
    //  appservice 
    subscribe(eventName: string, callback: any) {
      logger.debug('subscribe', eventName);

      subscribeEmitter.on(eventName, callback);
    },
    // 
    on(api: string, callback: any) {
      logger.debug('on', api);

      onEmitter.on(api, callback);
    },
  };
}
