import convertBin from 'licia/convertBin';
import axios from 'axios';
import bytesToStr from 'licia/bytesToStr';

export async function getFileStr(path: string) {
  const data = convertBin(await getFile(path), 'Uint8Array');

  return bytesToStr(data);
}

export async function getFile(path: string) {
  const response = await axios.get(`/app${path}`, {
    responseType: 'arraybuffer',
  });

  return response.data;
}

export function getUa(ua = '') {
  return `${ua} wechatdevtools/1.0 MicroMessenger/7.0.3`;
}
