import { EventEmitter } from 'events';

const darkLoading =
  'data:image/gif;base64,';
const lightLoading =
  'data:image/gif;base64,';
const PULL_DOWN_MAX_DISTANCE = 100;
const PULL_DOWN_VALID_DISTANCE = 50;

class PulldownRefresh extends EventEmitter {
  private isEnabled: boolean = false;
  private loadingDom: HTMLDivElement;
  private iconDom: HTMLElement;
  private isTouch: boolean = false;
  private startY: number = 0;
  private delta: number = 0;
  constructor() {
    super();

    this.loadingDom = document.createElement('div');
    this.iconDom = document.createElement('i');
    Object.assign(this.loadingDom.style, {
      height: '0px',
      width: '100%',
      position: 'fixed',
      top: '0px',
    });
    Object.assign(this.iconDom.style, {
      width: '32px',
      position: 'absolute',
      height: '6px',
      left: '50%',
      bottom: '20px',
      backgroundRepeat: 'no-repeat',
      marginLeft: '-16px',
      backgroundSize: 'cover',
    });
    this.loadingDom.appendChild(this.iconDom);
  }
  enable() {
    this.isEnabled = true;
    this.bindEvents();
  }
  disable() {
    this.isEnabled = false;
  }
  toggle() {
    this.isEnabled ? this.disable() : this.enable();
  }
  trigger() {
    if (!this.isEnabled) {
      return;
    }
    this.insertLoadingDom();
    this.loadingDom.style.height = `${PULL_DOWN_VALID_DISTANCE}px`;
    window.document.body.style.transition = 'all linear 0';
    window.document.body.style.marginTop = `${PULL_DOWN_VALID_DISTANCE}px`;
    this.emit('pulldown-refresh');
  }
  setStyle(style: string) {
    this.iconDom.style.backgroundImage = `url(${
      style !== 'light' ? darkLoading : lightLoading
    })`;
  }
  setBackgroundColor(color: string) {
    this.loadingDom.style.backgroundColor = color;
  }
  reset() {
    const body = window.document.body;
    if (!body) return;
    Object.assign(body.style, {
      transition: 'all linear 0.3s',
      marginTop: '0px',
    });
    Object.assign(this.loadingDom.style, {
      transition: 'all linear 0.3s',
      height: '0px',
    });
  }
  private bindEvents() {
    window.addEventListener('touchstart', this.handleTouchStart, true);
    window.addEventListener('touchmove', this.handleTouchMove, true);
    window.addEventListener('touchend', this.handleTouchEnd, true);
  }
  private handleTouchStart = (e: TouchEvent) => {
    if (window.scrollY !== 0) {
      return;
    }

    this.insertLoadingDom();
    this.isTouch = true;
    this.startY = e.touches[0].pageY;
    window.document.body.style.transition = 'all linear 0';
    this.loadingDom.style.transition = 'all linear 0';
  };
  private handleTouchMove = (e: TouchEvent) => {
    if (!this.isTouch) {
      return;
    }

    let delta = e.touches[0].pageY - this.startY;
    delta = Math.max(0, delta);
    delta = Math.min(PULL_DOWN_MAX_DISTANCE, delta);
    this.delta = delta;

    window.document.body.style.marginTop = `${delta}px`;
    this.loadingDom.style.height = `${delta}px`;
  };
  private handleTouchEnd = (e: TouchEvent) => {
    this.isTouch = false;

    if (this.delta > PULL_DOWN_VALID_DISTANCE) {
      this.emit('pulldown-refresh');

      this.delta = PULL_DOWN_VALID_DISTANCE;
      window.document.body.style.marginTop = `${this.delta}px`;
      this.loadingDom.style.height = `${this.delta}px`;

      setTimeout(() => {
        this.reset();
      }, 3000);
    } else {
      this.reset();
    }
  };
  private insertLoadingDom() {
    document.body.insertBefore(this.loadingDom, document.body.firstChild);
  }
}

export default new PulldownRefresh();
